export default {
	props:{
		noback:{
			type:Boolean,
			default:true
		}
	},
	data() {
		return {
			providerList: []
		}
	},
	onReady() {



	},
	methods:{

		mpGetUserInfo(result) {
			uni.showLoading({ title: '登录中...', mask: true });
			// 获取失败
			if (result.detail.errMsg !== 'getUserInfo:ok') {
				uni.hideLoading();
				uni.showModal({
					title: '获取用户信息失败',
					content: '错误原因' + result.detail.errMsg,
					showCancel: false
				});
				return;
			}
			let userinfo = result.detail.userInfo;
			uni.login({
				provider:"weixin",
				success: (res) => {
					this.MpLogin({
						url:"/wxlogin",
						code:res.code,
						nickName:userinfo.nickName,
						avatarUrl:userinfo.avatarUrl
					})
				},
				complete: () => {
					uni.hideLoading();
				}
			})
		},

		MpLogin(options){
			this.$http.post(options.url,{
				code:options.code,
				nickName:options.nickName,
				avatarUrl:options.avatarUrl
			}).then(data=>{
				let [err2,res2] = data;
				// 登录失败
				if (!this.$http.errorCheck(err2,res2)){
					return false;
				}
				console.log(res2.data.data)
				// 登录成功 保存状态
				this.User.token = res2.data.data.token;
				this.User.userinfo = this.User.__formatUserinfo(res2.data.data);
				// 本地存储
				uni.setStorageSync("userinfo",this.User.userinfo);
				uni.setStorageSync("token", this.User.token);
				// 获取用户相关统计
				if (this.User.userinfo.id) {
					this.User.getCounts();
				}
				// 连接socket
				if (this.User.userinfo.id) {
					this.$chat.Open();
				}
				// 成功提示
				uni.showToast({ title: '登录成功' });
				// 登录成功，重新加载数据
				this.$emit('logining')
				if (!this.noback) {
					uni.navigateBack();
				}
			});
		},
		// 获取当前登录渠道
		getLoginAuth(){
			uni.getProvider({
				service: 'oauth',
				success: (result) => {
					this.providerList = result.provider.map((value) => {
						let providerName = '';
						let icon='';
						switch (value) {
							case 'weixin':
								providerName = '微信登录';
								icon='weixin';
								break;
							case 'qq':
								providerName = 'QQ登录';
								icon='QQ';
								break;
							case 'sinaweibo':
								providerName = '新浪微博登录';
								icon='xinlangweibo';
								break;






						}
						return {
							name: providerName,
							icon:icon,
							id: value
						}
					});
					
				},
				fail: (error) => {
					console.log('获取登录通道失败', error);
				}
			});
		},
		// 登录
		tologin(provider) {
			uni.login({
				provider: provider.id,



				success: (res) => {
					uni.showLoading({
						title: '登陆中...',
						mask: false
					});
					/*
					{
						"authResult":{
							"access_token":"21_AugyUhnuM1xV7uyteOB9bwGiYSmTXFNGuRdILKlT__px2-ntmVKOMwpzQS1pZ2OScGfEMTVnEjCEgWBBpPNvdOkxO_oVU5x2MVHvU8iGxbY",
							"expires_in":7200,
							"refresh_token":"21_FnpsnuNn7LdtFr3rrwFE6djQwFmtfovPMWCT0oOgRivR3hC7AVPxn6i4n2koYWzQpnWnYF5NPMMtk4_RDX5DMJ0JVD1I3Kj4HPVQFGDcRmc",
							"openid":"oRrdQt3HS3Ns2TFCVLMOyxbR9DcM",
							"scope":"snsapi_userinfo",
							"unionid":"oU5Yytz7ielo7Xw00LMgZsPeSDkQ"
						},
						"errMsg":"login:ok"
					}
					*/
					console.log(res)
					
					uni.getUserInfo({
					  provider:provider.id,
					  success: (infoRes)=> {
						  console.log(infoRes)
						  //支付宝登录








						  /*
						  * {
						  	"openId":"oRrdQt3HS3Ns2TFCVLMOyxbR9DcM",
						  	"nickName":"楚绵",
						  	"gender":1,
						  	"city":"北帕默斯顿",
						  	"province":"北部地区",
						  	"country":"澳大利亚",
						  	"avatarUrl":"http://thirdwx.qlogo.cn/mmopen/vi_32/WiaWkkJjnG4WhI2KERDGPanF9GlNM3SWDTibibKEuHru1Jrd4pfGwialjn5tTCVLvEOq8RnZ8QmqkxyNAYXtuuGcBg/132",
						  	"unionId":"oU5Yytz7ielo7Xw00LMgZsPeSDkQ"
						  },
						  * 
						  * */
						// app登录

						console.log(JSON.stringify(infoRes.userInfo))
						let data = this.User.__formatOtherLogin(provider.id,Object.assign(infoRes,res));
						this.loginEvent(data);

					  }
					});
					
					// 更新保存在 store 中的登录状态
					console.log('登录成功，保存到本地存储，修改当前用户登录状态')
				},
				fail: (err) => {
					console.log('login fail:', err);
				},
				complete:()=>{
					uni.hideLoading();
				}
			});
		},
		async loginEvent(data){
			let res = await this.User.login({
				url:'/user/otherlogin',
				data:data,
				Noback:this.noback
			});
			if (res) {
				// 登录成功，重新加载数据
				this.$emit('logining')
			}
		}
	}
}
